#header{
    display: grid;
    background-color: #282828;
    border-bottom-style:solid;
    min-height: 60px;
    grid-template-columns: repeat(3, 1fr);
}

#title img{
    width: 500px;
}


#headerTitle{
    text-align: center;
    font-size: 45px;
    background-image: linear-gradient(to bottom left, #553c9a, #ee4b2b);
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
    font-weight: 600;
}

#aboutTitle{
    float:right;
    padding-top: 10px;
    padding-right: 20px;
    font-size: 30px;
    font-weight: 700;
    color: #FFFFFF;
}


@media only screen and (max-width: 900px){

    #aboutTitle{
        font-size: 25px;
    }
    #header{
        min-height: 35px;
    }
    #headerTitle{
        font-size: 30px;
    }
}

